.steps{
    
    @include tablet{
        padding: 60px 0;
    }
        
    @include landscape{
        padding: 40px !important;
    }
    @include mobile-xl{
        padding: 40px 0 !important;
        margin: 0;
    }
}
.steps__section-title {
	margin-bottom: 30px;
}
.steps-wrapper{
    display: flex;
    max-width: 100%;
    width: 100%;
    height: auto;
    margin: auto;
    align-items: center;
    margin-top: 40px;
    @include mobile-xl{
        flex-direction: column;
    }
	.text-part__container{
		width: 100%;
        max-width: 1098px;
		display: flex;
    	justify-content: center;
        position: relative;
        padding: 24px 0;
        margin-left: 83px;
        &::before{
            content: '';
            position: absolute;
            background-color: $black;
            width: 4px;
            height: 50%;
            top: 0;
            left: 23px;
            transform: translateX(-50%);
        }
        &::after{
            content: '';
            position: absolute;
            background-color: $black;
            width: 4px;
            height: 50%;
            bottom: 0;
            transform: translateX(-50%);
            left: 23px;
        }
	}
    .step__container{
        max-width: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        position: relative;

        &:first-child{

            .text-part__container{

                &::before{
                    content: none;
                }
            }

        }
    }
    .text-part{
        display: flex;
        align-items: center;
        width: 100%;
        height: 250px;
    
        @include tablet{
            max-width: 100%;
            padding: 0px 16px;
        }
        @include mobile-xl{
            max-width: 100% !important;
            width: 100%;
            padding: 0 36px;
        }
        .number{
            max-width: 46px;
            width: 100%;
            height: 46px;
            background-color: $white;
            font-size: 20px;
            line-height: 25px;
            font-family: "Favorit", sans-serif;
            font-weight: 600;
            border: 4px solid $black;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 5px 11px;
            border-radius: 50%;
            position: relative; 
            z-index: 3;
            
        }
        .text-part__title{
			margin-bottom: 50px;
        }
        .text-part__description{
            max-width: 990px;
            width: 100%;
            margin-left: 55px;
        }

    }

    .steps__image{

        height: auto;
        position: relative;
        max-width: 425px;
        width: 100%;
        height: 250px;
        text-align: center;
        img{
            height: 100%;
            width: 100%;
            -o-object-fit: cover;
                object-fit: cover;
            -o-object-position: center;
                object-position: center;
            position: relative;

        }
        @include landscape{
            width: 412px;
        }
        @include mobile-xl{
            width: 288px;
        }
    }
    .video-part{
        @include laptop{
            max-width: 480px;
            width: 100%;
        }
        video{
            max-width: 100%;
            width: 100%;
        }
    }
}
.steps-wrapper--row{
    flex-direction: column;
    @include landscape{
        flex-direction: column;
    }
}
.steps-wrapper--row-reverse{
    flex-direction: row-reverse;
}
.steps__cta-wrapper{
    @include tablet{
        margin-bottom: 28px;
    }
    &.steps__cta-margin-top{
        margin-top: 30px;
    }
}
